<template>
  <div :ref="chartName" class="applicationClassificationStaic">
    <div class="title">
      <img src="../../assets/imgs/overview/title.png" alt="" />
      <span>应用情况分类</span>
    </div>
    <div class="wrap">
      <div class="left">
        <div class="li">
          <img class="lta" src="../../assets/imgs/lta.png" alt="" />
          <img class="rba" src="../../assets/imgs/rba.png" alt="" />
          <span class="color-1">{{ access_num }}</span>
          <div>应用总数</div>
        </div>
        <div class="li">
          <img class="lta" src="../../assets/imgs/lta.png" alt="" />
          <img class="rba" src="../../assets/imgs/rba.png" alt="" />
          <span class="color-2">{{ amount }}</span>
          <div>应用活跃人数</div>
        </div>
      </div>
      <div class="right">
        <div v-show="isShowChart1" class="chart" :ref="chartName1" />
        <div v-if="!isShowChart1" class="noData">
          <img src="../../assets/imgs/index/nodata.png" alt="" />
          <p>暂无数据</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import Api from "../../utils/api";
//继承的基类
import DW from "../../../static/lib/DW";
export default {
  data() {
    return {
      chartName1: this.chartName + "1",
      chart1: this.chartName + "1",
      isShowChart1: true,
      dw: null,
      uid: "",
      id: this.id, //区域id或学校id
      type: 1, //类型 区域1 学校2
      provider_id: this.provider_id, //证书id
      access_num: 0, //接入数量
      amount: 0, //活跃人次
    };
  },
  watch: {
    $route() {},
  },
  props: {
    chartName: {
      type: String,
    },
    base_url: {
      type: String,
      default: "",
    },
    token: {
      type: String,
      default: "",
    },
  },
  methods: {
    // 获取数据
    async getData() {
      const that = this;
      return new Promise((resolve) => {
        Api.applicationAccess.getApiData(
          "applicationAccess",
          { id: that.id, type: that.type, provider_id: that.provider_id },
          function (data) {
            const { access_num, amount, list } = data;
            that.access_num = access_num;
            that.amount = amount;
            if (list.length == 0 || list.source.length === 0) {
              that.isShowChart1 = false;
            } else {
              resolve(list);
            }
          }
        );
      });
    },
    // 绘制图表 必须
    async _drawLine() {
      let that = this;
      //getTheme获取主题；
      this.chart1 = echarts.init(
        that.$refs[`${that.chartName1}`],
        that.dw.getTheme()
      );

      const dataset = await this.getData();
      // 设置图表配置
      const clientlabels = [];
      const indicator = [];

      let sortSoure = dataset.source.sort((a, b) => {
        return b[1] - a[1];
      });
      let max = sortSoure[0][1];

      dataset.source.forEach((item) => {
        indicator.push({ text: item[0], max });
        clientlabels.push(item[1]);
      });

      this.chart1.setOption({
        // dataset: [that.dw.getDataset()[0]],
        dataset: [dataset],
        backgroundColor: "transparent",
        tooltip: {
          trigger: "axis",
        },
        radar: [
          {
            indicator: indicator,
            center: ["50%", "50%"],
            radius: "50%",
          },
        ],
        series: [
          {
            type: "radar",
            tooltip: {
              trigger: "item",
            },
            areaStyle: {},
            data: [
              {
                value: clientlabels,
                name: "应用情况分类",
              },
            ],
          },
        ],
      });
    },
    // 初始化 必须
    init() {
      this._dataInit();
      this.dw.resetDataset().init();
    },
    // 重渲染 必须
    reload() {
      this._dataInit();
      this.dw.render();
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 更改主题 必须
    changeTheme(theme) {
      let that = this;
      let blo = false;
      this.dw.getThemes().forEach(function (el) {
        if (el.id == theme) {
          blo = true;
        }
      });
      if (blo) {
        that.dw.setTheme(theme);
        try {
          that.chart1.dispose();
          that.chart2.dispose();
        } catch (e) {}
        that.dw.resetDataset("").init();
      } else {
        console.log(`${theme}该主题不存在`);
      }
    },
    // 设置其他参数 必须
    setSettings(obj) {
      this.dw.setSettings(obj);
    },
    // 设置机构id 必须
    setRangeId(obj) {
      this.dw.setRangeId(obj);
    },
    // 设置时间范围 必须
    setDateRange(obj) {
      this.dw.setDateRange(obj);
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 销毁图表及数据初始化 必须
    _dataInit() {
      try {
        this.chart1.dispose();
        this.chart2.dispose();
      } catch (e) {}
    },
  },
  mounted: function () {
    let that = this;
    //创建实例 组件ref,api根路径,鉴权token 必须
    that.dw = new DW(`${that.chartName}`, that.base_url, that.token);
    //请求数据 必须
    that.dw.load_data = async function () {
      // let assetsData = await that.main_statistic();
      // let consumeData = await that.consume_statistic();
      let assetsData = [];
      let consumeData = [];
      return new Promise((resolve) => {
        resolve([assetsData, consumeData]);
      });
    };
    //渲染页面 必需
    that.dw.render = async function () {
      //除了绘制图表其他的事
      // that._doFn();
      //绘制图表
      that._drawLine();
    };
    //定义主题 非必须
    that.dw.getThemes = function () {
      return [
        { id: "dark", name: "暗色主题" },
        { id: "light", name: "亮色主题" },
      ];
    };
  },
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
@function px2em($px) {
  @return ($px/75) * 1rem;
}
@mixin textOver {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.color-1 {
  color: #fd3b73;
}
.color-2 {
  color: #6472ff;
}
.color-3 {
  color: #06d2c1;
}
.chart {
  width: 100%;
  height: 100%;
  padding: 0.1rem 0.2rem;
  box-sizing: border-box;
}
.applicationClassificationStaic {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 0.3rem 0.3rem 0.2rem 0.3rem;
  background: url("../../assets/imgs/school/wrapschool.png") no-repeat top
    center/100% 100%;
  .title {
    padding-bottom: 0.1rem;
    color: #c8cdff;
    img {
      margin-right: 0.1rem;
    }
  }
  .wrap {
    flex: 1;
    display: flex;
    .left {
      .li {
        width: 2rem;
        height: 1.2rem;
        background: rgba($color: #343e9b, $alpha: 0.1);
        margin-top: 0.3rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        color: #e5e5e5;
        position: relative;
        .rba {
          position: absolute;
          bottom: 0;
          right: 0;
        }
        .lta {
          position: absolute;
          top: 0;
          left: 0;
        }
        span {
          font-size: 0.48rem;
        }
      }
    }
    .right {
      position: relative;
      flex: 1;
    }
  }
}
</style>
