<template>
  <!-- 某某部门教育数据综合分析 -->
  <div>
    <div class="head" :class="{ headP: $route.name == 'trackEvents' }">
      <span>部门教育数据综合分析</span>
      <div class="back" @click="$router.go(-1)">
