          />
        </div>
        <div class="mod2">
          <resourcesTotalStaic
            :ref="chart2"
            :chartName="chart2"
            :base_url="base_url"
            :token="token"
          />
        </div>
      </div>
      <div class="wrap-right">
        <div class="right-top">
          <div class="mod3">
            <applicationClassificationStaic
              :ref="chart3"
