        </div>
        <!-- <div class="mod5">
                <equipmentClassificationAndStatistics :ref="chart5" :chartName="chart5" :base_url="base_url" :token="token"/>
            </div> -->
        <div class="right-bottom">
          <div class="mod5">
            <aYearAassetsStatistics
              :ref="chart5"
              :chartName="chart5"
              :base_url="base_url"
              :token="token"
            />
          </div>
          <div class="mod6">
            <assetStatus
              :ref="chart6"
              :chartName="chart6"
              :base_url="base_url"
              :token="token"
            />
          </div>
        </div>
      </div>
