<script>
import axios from "axios";
import schoolTypeStaic from "../../components/sectordata/schoolTypeStaic"; //学校类型
import resourcesTotalStaic from "../../components/sectordata/resourcesTotalStaic"; //资源总数
import applicationClassificationStaic from "../../components/sectordata/applicationClassificationStaic"; //应用情况分类
import segmentDistributionStaic from "../../components/sectordata/segmentDistributionStaic"; //学段分布
// import equipmentClassificationAndStatistics from '../../components/sectordata/equipmentClassificationAndStatistics'//设备分类及统计
import aYearAassetsStatistics from "../../components/sectordata/aYearAassetsStatistics.vue";
import assetStatus from "../../components/sectordata/assetStatus.vue";

export default {
  components: {
    schoolTypeStaic,
    resourcesTotalStaic,
    applicationClassificationStaic,
    segmentDistributionStaic,
    aYearAassetsStatistics,
    assetStatus,
  },

  data() {
    return {
      chart1: "schoolTypeStaic",
      chart2: "resourcesTotalStaic",
      chart3: "applicationClassificationStaic",
      chart4: "segmentDistributionStaic",
      chart5: "aYearAassetsStatistics",
      chart6: "assetStatus",
      base_url: "",
      token: "EvnStudkey",
      weatherList: [],
      dateInfo: "",
    };
  },

  wacth: {},

  created() {
    const that = this;
    this.dateInit();
    this.timer = setInterval(function () {
      that.dateInit();
    }, 1000);
    this.getWeather();
  },
  beforeDestroy() {
    if (this.timer) {
      //如果定时器还在运行 或者直接关闭，不用判断
      clearInterval(this.timer); //关闭
    }
  },
  mounted() {
    this.mockLoad();
  },

  computed: {},

  methods: {
    defaultLoad() {
      this.$refs.schoolTypeStaic.init();
    },
    mockLoad() {
      let mock =
        '[{"dimensions":[{"name":"date","type":"ordinal","displayName":"日期"},{"name":"main_total","type":"number","displayName":"资产总数"},{"name":"month_main_total","type":"number","displayName":"借出资产数量"},{"name":"loan_main_total","type":"number","displayName":"新增资产数量"}],"source":[["2021-03","73","2","19"],["2021-04","73","2","19"],["2021-05","103","23","36"],["2021-06",104,1,36]],"sourceHeader":false},{"dimensions":[{"name":"date","type":"ordinal","displayName":"日期"},{"name":"main_total","type":"number","displayName":"资产总数"},{"name":"month_main_total","type":"number","displayName":"借出资产数量"},{"name":"loan_main_total","type":"number","displayName":"新增资产数量"}],"source":[["2021-03","73","2","19"],["2021-04","73","2","19"],["2021-05","103","23","36"],["2021-06",104,1,36]],"sourceHeader":false}]';
      this.$refs.schoolTypeStaic.setMock(mock);
      this.$refs.schoolTypeStaic.init();
      this.$refs.resourcesTotalStaic.setMock(mock);
      this.$refs.resourcesTotalStaic.init();
      this.$refs.applicationClassificationStaic.setMock(mock);
      this.$refs.applicationClassificationStaic.init();
      this.$refs.segmentDistributionStaic.setMock(mock);
      this.$refs.segmentDistributionStaic.init();
      // this.$refs.equipmentClassificationAndStatistics.setMock(mock);
      // this.$refs.equipmentClassificationAndStatistics.init()
      this.$refs.aYearAassetsStatistics.setMock(mock);
      this.$refs.aYearAassetsStatistics.init();

      this.$refs.assetStatus.setMock(mock);
      this.$refs.assetStatus.init();
    },
    getWeather() {
      var that = this;
      axios
        .get(
          "https://www.tianqiapi.com/api/?appid=84823855&appsecret=zZG440Am&version=v1&cityid=101270101"
        )
        .then(function (res) {
          that.weatherList = res.data.data[0];
        })
        .catch(() => {});
    },
    dateInit: function () {
      const date = new Date();
      const Y = date.getFullYear();
      const M =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      const D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      const h = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
      const m =
        date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
      const s =
        date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
      const weekArr = ["周日", "周一", "周二", "周三", "周四", "周五", "周六"];
      this.dateInfo =
        Y +
        "年" +
        M +
        "月" +
        D +
        "日" +
        " " +
       
        " " +
        h +
        ":" +
        m +
         " " +
        weekArr[date.getDay()] ;
    },
  },
};
</script>
