<template>
  <div :ref="chartName" class="assetStatus">
      <div v-show="isShowChart1"  class="chart" :ref="chartName1" />
      <div v-if="!isShowChart1" class="noData">
          <img src="../../assets/imgs/index/nodata.png" alt="" />
          <p>暂无数据</p>
        </div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import Api from "../../utils/api";
//继承的基类
import DW from "../../../static/lib/DW";
export default {
  data() {
    return {
      chartName1: this.chartName + "1",
      chart1: this.chartName + "1",
      isShowChart1:true,
      dw: null,
      district_id:this.district_id,
      provider_id:this.provider_id
    };
  },
  watch: {
    $route() {}
  },
  props: {
    chartName: {
      type: String
    },
    base_url: {
      type: String,
      default: ""
    },
    token: {
      type: String,
      default: ""
    }
  },
  methods: {
      // 获取数据
    async getData() {
      const that = this;
      return new Promise(resolve => {
        Api.PropertyStatusCensus.getApiData(
          "PropertyStatusCensus",
          { district_id: that.district_id,provider_id:that.provider_id},
          function(data) {
            if(data&&data.source.length===0){
              that.isShowChart1 = false
            }
            resolve(data);
          }
        );
      });
    },
    // 绘制图表 必须
    async _drawLine() {
      let that = this;
      //getTheme获取主题；
      this.chart1 = echarts.init(
        that.$refs[`${that.chartName1}`],
        that.dw.getTheme()
      );

      const dataset = await this.getData()
      this.chart1.setOption({
        //getDataset获取数据；
        dataset: [dataset],
        backgroundColor: "transparent",
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985"
            }
          }
        },
         title: {
          text: "{a|     资产状态}",
          show: true,
          textStyle: {
            color: "#C8CDFF",
            rich: {
              a: {
                fontSize: "100%",
                width: 15,
                height: 14,
                backgroundColor: {
                  image: require("../../assets/imgs/title.png")
                }
              }
            }
          }
        },
         color: ['#FF9966', '#FFFFCC', '#0099CC', '#99CC33', '#99CCCC'],
      tooltip: {
          trigger: 'item',
          formatter: "{c}({d}%)",
          textStyle: {
              fontSize: 15,
          },
    },
    legend: {
      type:'scroll',
        right: '5%',
        top: '8%',
        itemWidth: 10,
        itemHeight: 10,
        itemGap: 10,
        textStyle: {
            color: '#',
            fontSize: 14,
        },
    },
    series: [{
        name: 'TITLE',
        type: 'pie',
        clockwise: false,
        startAngle: 90,
        radius: '35%',
        center: ['50%', '60%'],
        hoverAnimation: false,
        roseType: 'radius', //area
        itemStyle: {
            normal: {
                borderColor: '#273454',
                borderWidth: '5',
            },
        },
        label: {
            show: true,
            position: 'outside',
            formatter: '{a|{b}：{d}%}\n{hr|}',
            rich: {
                hr: {
                    backgroundColor: 't',
                    borderRadius: 100,
                    width: 0,
                    height: 10,
                    padding: [3, 3, 0, -16],
                    shadowColor: '#1c1b3a',
                    shadowBlur: 1,
                    shadowOffsetX: '0',
                    shadowOffsetY: '2',
                },
                a: {
                    padding: [-35, 15, -20, 5],
                }
            }
        },
        labelLine: {
            normal: {
                length: 20,
                length2: 30,
                lineStyle: {
                    width: 1,
                }
            }
        },
        
    }],
      

 
      });
   },
    // 初始化 必须
    init() {
      this._dataInit();
      this.dw.resetDataset().init();
    },
    // 重渲染 必须
    reload() {
      this._dataInit();
      this.dw.render();
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 更改主题 必须
    changeTheme(theme) {
      let that = this;
      let blo = false;
      this.dw.getThemes().forEach(function(el) {
        if (el.id == theme) {
          blo = true;
        }
      });
      if (blo) {
        that.dw.setTheme(theme);
        try {
          that.chart1.dispose();
          that.chart2.dispose();
        } catch (e) {}
        that.dw.resetDataset("").init();
      } else {
        console.log(`${theme}该主题不存在`);
      }
    },
    // 设置其他参数 必须
    setSettings(obj) {
      this.dw.setSettings(obj);
    },
    // 设置机构id 必须
    setRangeId(obj) {
      this.dw.setRangeId(obj);
    },
    // 设置时间范围 必须
    setDateRange(obj) {
      this.dw.setDateRange(obj);
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 销毁图表及数据初始化 必须
    _dataInit() {
      try {
        this.chart1.dispose();
        this.chart2.dispose();
      } catch (e) {}
    }
  },
  mounted: function() {
    let that = this;
    //创建实例 组件ref,api根路径,鉴权token 必须
    that.dw = new DW(`${that.chartName}`, that.base_url, that.token);
    //请求数据 必须
    that.dw.load_data = async function() {
      // let assetsData = await that.main_statistic();
      // let consumeData = await that.consume_statistic();
      let assetsData = [];
      let consumeData = [];
      return new Promise(resolve => {
        resolve([assetsData, consumeData]);
      });
    };
    //渲染页面 必需
    that.dw.render = async function() {
      //除了绘制图表其他的事
      // that._doFn();
      //绘制图表
      that._drawLine();
    };
    //定义主题 非必须
    that.dw.getThemes = function() {
      return [
        { id: "dark", name: "暗色主题" },
        { id: "light", name: "亮色主题" }
      ];
    };
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.assetStatus {
  position: relative;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 0.3rem 0.2rem;
  background: url("../../assets/imgs/school/wrap.png") no-repeat top center/100%
    100%;
    .chart{
        width: 100%;
        height: 100%;
        box-sizing: border-box;
    }
}
</style>
