<template>
  <div  :ref="chartName" class="aYearAassetsStatistics">
    <div class="title">
      <img src="../../assets/imgs/overview/title.png" alt="" />
      <span>资产近一年数量统计</span>
    </div>
    <div class="center">
      <div class="num">{{total}}</div>
      <div class="txt">资产总数量</div>
    </div>
    <div class="bottom">
      <div v-show="isShowChart1" class="chart" :ref="chartName1" />
       <div v-if="!isShowChart1" class="noData">
          <img src="../../assets/imgs/index/nodata.png" alt="" />
          <p>暂无数据</p>
        </div>
    </div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import Api from "../../utils/api";
//继承的基类
import DW from "../../../static/lib/DW";
export default {
  data() {
    return {
      chartName1: this.chartName + "1",
      chart1: this.chartName + "1",
      isShowChart1:true,
      dw: null,
      district_id:this.district_id,
      provider_id:this.provider_id,
      total:0
    };
  },
  watch: {
    $route() {}
  },
  props: {
    chartName: {
      type: String
    },
    base_url: {
      type: String,
      default: ""
    },
    token: {
      type: String,
      default: ""
    }
  },
  methods: {
      // 获取数据
    async getData() {
      const that = this;
      return new Promise(resolve => {
        Api.PropertyCensusTrend.getApiData(
          "PropertyCensusTrend",
          { district_id: that.district_id,provider_id:that.provider_id},
          function(data) {
            const { total,list } = data;
            that.total = total;
            if(list&&list.source.length===0){
              that.isShowChart1 = false
            }
            resolve(list);
          }
        );
      });
    },
    // 绘制图表 必须
   async  _drawLine() {
      let that = this;
      //getDataset获取数据；
      const dataset = await this.getData()
      this.chart1 = echarts.init(
        that.$refs[`${that.chartName1}`],
        that.dw.getTheme()
      );
      this.chart1.setOption({
       
        dataset: [dataset],
        backgroundColor: "transparent",
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985"
            }
          },
          formatter(params){
            for (const key in params) {
                return params[key].name + '新增资产：'+ params[key].value[1]
            }
          }
        },
        grid: {
          top: "18%",
          left: "5%",
          right: "5%",
          bottom: "0%",
          containLabel: true
        },

        xAxis: [
          {
            type: "category",
            axisLine: {
              show: true,
              color: "#8FA3AB",
              lineStyle: {
                color: "rgba(102, 153, 255, 0.6)"
              }
            },
            axisLabel: {
              color: "#516F95"
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: "#E2E6E9"
              }
            },
            axisTick: {
              show: false
            },
            boundaryGap: true,
            // data: ["1月", "2月", "3月", "4月", "5月", "6月"]
          }
        ],

        yAxis: [
          {
            type: "value",
            min: 0,
            splitLine: {
              show: true,
              lineStyle: {
                //color: 'rgba(255,255,255,0.1)'
                color: "#E2E6E9"
              }
            },
            axisLine: {
              show: true,
              color: "#8FA3AB",
              lineStyle: {
                color: "rgba(102, 153, 255, 0.6)"
              }
            },
            axisLabel: {
              show: true,
              textStyle: {
                color: "#516F95"
              }
            },
            axisTick: {
              show: false
            }
          }
        ],
        series: [
          {
            name: "",
            type: "line",
            smooth: true, //是否平滑
            showAllSymbol: true,
            symbol: "circle",
            symbolSize: 8,
            lineStyle: {
              normal: {
                color: "#4ECFE3",
                shadowColor: "rgba(144, 196, 234, .9)",
                shadowBlur: 5,
                shadowOffsetY: 2,
                shadowOffsetX: 2
              }
            },
            label: {
              show: false,
              position: "top",
              textStyle: {
                color: "#05D2D7"
              }
            },
            itemStyle: {
              color: "#fff",
              borderColor: "#30B2C6",
              borderWidth: 3
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(
                  0,
                  0,
                  0,
                  1,
                  [
                    {
                      offset: 0,
                      color: "rgba(102, 153, 255, 1)"
                    },
                    {
                      offset: 0.3,
                      color: "rgba(102, 153, 255, 0.4)"
                    },
                    {
                      offset: 0.6,
                      color: "rgba(102, 153, 255, 0)"
                    },
                    {
                      offset: 1,
                      color: "rgba(102, 153, 255, 0)"
                    }
                  ],
                  false
                )
              }
            },
            // data: [0, 205.97, 332.79, 281.55, 398.35, 214.02]
          }
        ]
      });
   },
    // 初始化 必须
    init() {
      this._dataInit();
      this.dw.resetDataset().init();
    },
    // 重渲染 必须
    reload() {
      this._dataInit();
      this.dw.render();
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 更改主题 必须
    changeTheme(theme) {
      let that = this;
      let blo = false;
      this.dw.getThemes().forEach(function(el) {
        if (el.id == theme) {
          blo = true;
        }
      });
      if (blo) {
        that.dw.setTheme(theme);
        try {
          that.chart1.dispose();
          that.chart2.dispose();
        } catch (e) {}
        that.dw.resetDataset("").init();
      } else {
        console.log(`${theme}该主题不存在`);
      }
    },
    // 设置其他参数 必须
    setSettings(obj) {
      this.dw.setSettings(obj);
    },
    // 设置机构id 必须
    setRangeId(obj) {
      this.dw.setRangeId(obj);
    },
    // 设置时间范围 必须
    setDateRange(obj) {
      this.dw.setDateRange(obj);
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 销毁图表及数据初始化 必须
    _dataInit() {
      try {
        this.chart1.dispose();
        this.chart2.dispose();
      } catch (e) {}
    }
  },
  mounted: function() {
    let that = this;
    //创建实例 组件ref,api根路径,鉴权token 必须
    that.dw = new DW(`${that.chartName}`, that.base_url, that.token);
    //请求数据 必须
    that.dw.load_data = async function() {
      // let assetsData = await that.main_statistic();
      // let consumeData = await that.consume_statistic();
      let assetsData = [];
      let consumeData = [];
      return new Promise(resolve => {
        resolve([assetsData, consumeData]);
      });
    };
    //渲染页面 必需
    that.dw.render = async function() {
      //除了绘制图表其他的事
      // that._doFn();
      //绘制图表
      that._drawLine();
    };
    //定义主题 非必须
    that.dw.getThemes = function() {
      return [
        { id: "dark", name: "暗色主题" },
        { id: "light", name: "亮色主题" }
      ];
    };
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.aYearAassetsStatistics {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 0.3rem 0.3rem 0.2rem 0.3rem;
  background: url("../../assets/imgs/school/wrap.png") no-repeat top center/100%
    100%;
.chart{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    }
  .title {
    padding: 0.1rem 0;
    color: #c8cdff;
    img {
      margin-right: 0.1rem;
    }
  }
  .center {
    width: 1.96rem;
    height: 1rem;
    margin: 0.2rem auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    background: url("../../assets/imgs/hb.png") no-repeat top center/100% 100%;
    .num {
      color: #06d2c1;
      font-size: 0.36rem;
    }
    .txt {
      color: #c8cdff;
      font-size: 0.17rem;
    }
  }
  .bottom {
    position: relative;
    flex: 1;
  }
}
</style>