<template>
  <div :ref="chartName" class="resourcesTotalStaic">
    <div class="title">
      <img src="../../assets/imgs/overview/title.png" alt="" />
      <span>资源总数</span>
    </div>
    <div class="top">
      <div class="li">
        <span class="color-1">{{ resource_total }}</span>
        <div>平台资源总量</div>
      </div>
      <div class="li">
        <span class="color-2">{{ use_total }}</span>
        <div>使用人数</div>
      </div>
      <div class="li">
        <span class="color-3">{{ browse_total }}</span>
        <div>浏览数</div>
      </div>
    </div>
    <div class="bottom">
      <div class="chart1">
        <div class="chart" v-show="isShowChart1" :ref="chartName1" />
        <div v-if="!isShowChart1" class="noData">
          <!-- <img src="../../assets/imgs/index/nodata.png" alt="" /> -->
          <p>暂无数据</p>
        </div>
      </div>
      <div class="chart2">
        <div v-show="isShowChart2" class="chart" :ref="chartName2" />
        <div v-if="!isShowChart2" class="noData">
          <!-- <img src="../../assets/imgs/index/nodata.png" alt="" /> -->
          <p>暂无数据</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import Api from "../../utils/api";
//继承的基类
import DW from "../../../static/lib/DW";
export default {
  data() {
    return {
      chartName1: this.chartName + "1",
      chartName2: this.chartName + "2",
      chart1: this.chartName + "1",
      chart2: this.chartName + "2",
      isShowChart1: true,
      isShowChart2: true,
      dw: null,
      provider_id: this.provider_id,
      id: this.id,
      type: 1,
      resource_total: 0, //资源总数
      use_total: 0, //使用总数
      browse_total: 0, //浏览总数
    };
  },
  watch: {
    $route() {},
  },
  props: {
    chartName: {
      type: String,
    },
    base_url: {
      type: String,
      default: "",
    },
    token: {
      type: String,
      default: "",
    },
  },
  methods: {
    // 教育资源
    async getData() {
      const that = this;
      return new Promise((resolve) => {
        Api.ResourceCensus.getApiData(
          "ResourceCensus",
          {
            provider_id: that.provider_id,
            id: that.id,
            type: that.type,
          },
          function (data) {
            that.browse_total = data.browse_total;
            that.resource_total = data.resource_total;
            that.use_total = data.use_total;
            if (data.classify_census.source.length === 0) {
              that.isShowChart1 = false;
            }
            if (data.browse_census.source.length === 0) {
              that.isShowChart2 = false;
            }
            resolve([data.classify_census, data.browse_census]);
          }
        );
      });
    },
    // 绘制图表 必须
    async _drawLine() {
      let that = this;
      const [classify_census, browse_census] = await that.getData();
      let width = document.documentElement.getBoundingClientRect().width;
      let rem = (width * 100) / 1920;
      function getFontSize(fontSize) {
        return (fontSize * rem) / 100;
      }
      //getTheme获取主题；
      this.chart1 = echarts.init(
        that.$refs[`${that.chartName1}`],
        that.dw.getTheme()
      );
      this.chart1.setOption({
        //getDataset获取数据；
        dataset: [classify_census],
        color: ["#FF8080", "#FFFFFF", "#2C6DFF", "#FFE553", "#1CFFF0"],
        backgroundColor: "transparent",
        tooltip: {
          // trigger: "axis"
        },
        title: {
          text: "资源分类占比",
          show: true,
          left: "center",
          textStyle: {
            color: "#C8CDFF",
            fontSize: "0.12rem",
          },
        },
        series: [
          {
            type: "pie",
            center: ["50%", "62%"],
            name: "资产总数",
            smooth: true,
            // encode: {
            //     x: 'date',
            //     y: 'main_total'
            // },
            label: {
              normal: {
                show: false,
              },
            },
            labelLine: {
              normal: {
                show: false,
              },
            },
            areaStyle: {},
          },
        ],
      });
      this.chart2 = echarts.init(
        that.$refs[`${that.chartName2}`],
        that.dw.getTheme()
      );
      this.chart2.setOption({
        dataset: [browse_census],
        backgroundColor: "transparent",
        title: {
          text: "资源活跃学校排行",
          show: true,
          left: "center",
          textStyle: {
            color: "#C8CDFF",
            fontSize: "100%",
          },
        },
        tooltip: {
          // trigger: "axis"
        },
        grid: {
          top: getFontSize(37),
          left: getFontSize(6),
          bottom: getFontSize(10),
          right: getFontSize(6),
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            axisLine: {
              //坐标轴轴线相关设置。数学上的x轴
              show: true,
              lineStyle: {
                color: "#4D4D4D",
              },
            },
            axisLabel: {
              show: false,
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: "#4D4D4D",
                type: "dashed",
              },
            },
            axisTick: {
              show: false,
            },
            // data: [1, 2, 3, 4, 5]
          },
        ],
        yAxis: [
          {
            axisLine: {
              show: true,
              lineStyle: {
                color: "#233653",
              },
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: "#4D4D4D",
                type: "dashed",
              },
            },
            axisLabel: {
              show: false,
            },
            axisTick: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "",
            type: "line",
            symbol: "circle",
            symbolSize: getFontSize(8),
            itemStyle: {
              normal: {
                color: "#FFFFFF",
                borderWidth: getFontSize(3),
                borderColor: "#1CFFF0",
              },
            },
            lineStyle: {
              normal: {
                width: getFontSize(2),
                color: "#1CFFF0", // 线条颜色
              },
              borderColor: "rgba(0,0,0,.4)",
            },
            tooltip: {
              show: true,
            },
            encode: {
              // 将 "product" 列映射到 Y 轴。
              y: "total",
            },
            // data: ["40", "60", "22", "85", "50"]
          },
        ],
      });
    },
    // 初始化 必须
    init() {
      this._dataInit();
      this.dw.resetDataset().init();
    },
    // 重渲染 必须
    reload() {
      this._dataInit();
      this.dw.render();
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 更改主题 必须
    changeTheme(theme) {
      let that = this;
      let blo = false;
      this.dw.getThemes().forEach(function (el) {
        if (el.id == theme) {
          blo = true;
        }
      });
      if (blo) {
        that.dw.setTheme(theme);
        try {
          that.chart1.dispose();
          that.chart2.dispose();
        } catch (e) {}
        that.dw.resetDataset("").init();
      } else {
        console.log(`${theme}该主题不存在`);
      }
    },
    // 设置其他参数 必须
    setSettings(obj) {
      this.dw.setSettings(obj);
    },
    // 设置机构id 必须
    setRangeId(obj) {
      this.dw.setRangeId(obj);
    },
    // 设置时间范围 必须
    setDateRange(obj) {
      this.dw.setDateRange(obj);
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 销毁图表及数据初始化 必须
    _dataInit() {
      try {
        this.chart1.dispose();
        this.chart2.dispose();
      } catch (e) {}
    },
  },
  mounted: function () {
    let that = this;
    //创建实例 组件ref,api根路径,鉴权token 必须
    that.dw = new DW(`${that.chartName}`, that.base_url, that.token);
    //请求数据 必须
    that.dw.load_data = async function () {
      // let assetsData = await that.main_statistic();
      // let consumeData = await that.consume_statistic();
      let assetsData = [];
      let consumeData = [];
      return new Promise((resolve) => {
        resolve([assetsData, consumeData]);
      });
    };
    //渲染页面 必需
    that.dw.render = async function () {
      //除了绘制图表其他的事
      // that._doFn();
      //绘制图表
      that._drawLine();
    };
    //定义主题 非必须
    that.dw.getThemes = function () {
      return [
        { id: "dark", name: "暗色主题" },
        { id: "light", name: "亮色主题" },
      ];
    };
  },
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
@function px2em($px) {
  @return ($px/75) * 1rem;
}
@mixin textOver {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.color-1 {
  color: #fd3b73;
}
.color-2 {
  color: #6472ff;
}
.color-3 {
  color: #06d2c1;
}
.chart {
  width: 100%;
  height: 100%;
}
.chart1 {
  width: 40%;
  height: 100%;
  // padding: 0.1rem 0.2rem;
  box-sizing: border-box;
  position: relative;
}
.chart2 {
  width: 60%;
  height: 100%;
  // padding: 0.1rem 0.2rem;
  box-sizing: border-box;
  position: relative;
}
.resourcesTotalStaic {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 0.3rem 0.3rem 0.2rem 0.3rem;
  background: url("../../assets/imgs/school/wrapschool.png") no-repeat top
    center/100% 100%;
  .title {
    padding-bottom: 0.1rem;
    color: #c8cdff;
    img {
      margin-right: 0.1rem;
    }
  }
  .top {
    flex: 1;
    margin-bottom: 0.1rem;
    display: flex;
    justify-content: space-between;
    padding: 0 0.5rem;
    align-items: center;
    .li {
      width: 1.46rem;
      height: 0.7rem;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      font-size: 0.12rem;
      color: #c8cdff;
      background: rgba(59, 157, 255, 0.14);
      background: url("../../assets/imgs/hb.png") no-repeat top center/100% 100%;
      margin-right: 0.24rem;
      &:last-child {
        margin-right: 0;
      }
      span {
        font-size: 0.26rem;
      }
    }
  }
  .bottom {
    flex: 1;
    display: flex;
    // background: rgba(100, 114, 255, 0.14);
    opacity: 1;
  }
}
</style>
