<template>
  <div :ref="chartName" class="segmentDistributionStaic">
    <div class="wrap">
      <img class="lta" src="../../assets/imgs/lta.png" alt="">
      <img class="rba" src="../../assets/imgs/rba.png" alt="">
      <div class="title">
        <img src="../../assets/imgs/overview/title.png" alt="" />
        <span>学段分布情况</span>
      </div>
      <!-- <div class="top">
        <div class="li">
          <div class="color-1"><span>100</span>人</div>
          <div>小学</div>
        </div>
        <div class="li">
          <div class="color-2"><span>257</span>人</div>
          <div>普通初中</div>
        </div>
        <div class="li">
          <div class="color-3"><span>789</span>人</div>
          <div>普通高中</div>
        </div>
      </div> -->
      <div class="bottom">
        <div v-show="isShowChart1" class="chart" :ref="chartName1" />
         <div v-if="!isShowChart1" class="noData">
          <img src="../../assets/imgs/index/nodata.png" alt="" />
          <p>暂无数据</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import Api from "../../utils/api";
//继承的基类
import DW from "../../../static/lib/DW";
export default {
  data() {
    return {
      chartName1: this.chartName + "1",
      chartName2: this.chartName + "2",
      chart1: this.chartName + "1",
      chart2: this.chartName + "2",
      isShowChart1:true,
      dw: null,
      provider_id: this.provider_id,
      district_id: this.district_id,
    };
  },
  watch: {
    $route() {},
  },
  props: {
    chartName: {
      type: String,
    },
    base_url: {
      type: String,
      default: "",
    },
    token: {
      type: String,
      default: "",
    },
  },
  methods: {
    // 获取数据
    async getData() {
      const that = this;
      return new Promise((resolve) => {
        Api.EducationSchoolCensus.getApiData(
          "EducationSchoolCensus",
          {
            provider_id: that.provider_id,
            district_id: that.district_id,
          },
          function (data) {
            if(data.list&&data.list.source.length===0){
              that.isShowChart1 = false
            }
            resolve(data.list);
          }
        );
      });
    },
    // 绘制图表 必须
    async _drawLine() {
      let that = this;
      //getTheme获取主题；
      const dataset = await that.getData();

      this.chart1 = echarts.init(
        that.$refs[`${that.chartName1}`],
        that.dw.getTheme()
      );
      this.chart1.setOption({
        //getDataset获取数据；
        dataset: [dataset],
        backgroundColor: "transparent",
        color:['#FD3B73','#6472FF','#06D2C1','#2C6DFF','#FEBC31','#FF7723'],
        tooltip: {
          trigger: "axis",
        },
        legend: {
          //  orient: 'vertical',
          type:'scroll',
          itemWidth: 10,
          itemHeight: 10,
          itemGap: 10,
          bottom: "bottom",
          left: "4%",
        },
        padding: [10, 10, 10, 10],
        series: [
          {
            type: "pie",
            center: ["50%", "50%"],
            radius: "40%",
            name: "资产总数",
            smooth: true,
            // encode: {
            //     x: 'date',
            //     y: 'main_total'
            // },
            label: {
               formatter: function(params){
                //  return  '{a|{b}：{d}%}'
                   return  params.name+params.value[1] +"人"+"\n" + "占比:" + params.percent+"%"
               },
            },
            labelLine: {
              normal: {
                show: true,
              },
            },
            areaStyle: {},
          },
        ],
      });
    },
    // 初始化 必须
    init() {
      this._dataInit();
      this.dw.resetDataset().init();
    },
    // 重渲染 必须
    reload() {
      this._dataInit();
      this.dw.render();
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 更改主题 必须
    changeTheme(theme) {
      let that = this;
      let blo = false;
      this.dw.getThemes().forEach(function (el) {
        if (el.id == theme) {
          blo = true;
        }
      });
      if (blo) {
        that.dw.setTheme(theme);
        try {
          that.chart1.dispose();
          that.chart2.dispose();
        } catch (e) {}
        that.dw.resetDataset("").init();
      } else {
        console.log(`${theme}该主题不存在`);
      }
    },
    // 设置其他参数 必须
    setSettings(obj) {
      this.dw.setSettings(obj);
    },
    // 设置机构id 必须
    setRangeId(obj) {
      this.dw.setRangeId(obj);
    },
    // 设置时间范围 必须
    setDateRange(obj) {
      this.dw.setDateRange(obj);
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 销毁图表及数据初始化 必须
    _dataInit() {
      try {
        this.chart1.dispose();
        this.chart2.dispose();
      } catch (e) {}
    },
  },
  mounted: function () {
    let that = this;
    //创建实例 组件ref,api根路径,鉴权token 必须
    that.dw = new DW(`${that.chartName}`, that.base_url, that.token);
    //请求数据 必须
    that.dw.load_data = async function () {
      // let assetsData = await that.main_statistic();
      // let consumeData = await that.consume_statistic();
      let assetsData = [];
      let consumeData = [];
      return new Promise((resolve) => {
        resolve([assetsData, consumeData]);
      });
    };
    //渲染页面 必需
    that.dw.render = async function () {
      //除了绘制图表其他的事
      // that._doFn();
      //绘制图表
      that._drawLine();
    };
    //定义主题 非必须
    that.dw.getThemes = function () {
      return [
        { id: "dark", name: "暗色主题" },
        { id: "light", name: "亮色主题" },
      ];
    };
  },
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
@function px2em($px) {
  @return ($px/75) * 1rem;
}
@mixin textOver {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.color-1 {
  color: #06d2c1;
}
.color-2 {
  color: #febc31;
}
.color-3 {
  color: #ff7723;
}
.chart {
  width: 100%;
  height: 100%;
  // padding: 0.1rem 0.2rem;
  box-sizing: border-box;
}
  .rba{
          position: absolute;
          bottom: 0;
          right: 0;
        }
         .lta{
          position: absolute;
          top: 0;
          left: 0;
        }
.segmentDistributionStaic {
  width: 100%;
  height: 100%;
  box-sizing: border-box;

  padding: 0.3rem 0.3rem 0.2rem 0.3rem;
  background: url("../../assets/imgs/school/wrap.png") no-repeat top center/100%
    100%;
  .wrap {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    padding: 0.1rem;
    background: rgba(59, 157, 255, 0.14);
    position: relative;
  }
  .title {
    padding-bottom: 0.1rem;
    color: #c8cdff;
    img {
      margin-right: 0.1rem;
    }
  }
  .top {
    // flex: 1;
    margin-bottom: 0.2rem;
    display: flex;
    justify-content: space-between;
    padding: 0 0.5rem;
    align-items: center;
    .li {
      width: 1.36rem;
      height: 0.9rem;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      font-size: 0.12rem;
      color: #c8cdff;
      background: rgba(59, 157, 255, 0.14);
      background: url("../../assets/imgs/hb.png") no-repeat top center/100% 100%;
      margin-right: 0.24rem;
      &:last-child {
        margin-right: 0;
      }
      span {
        font-size: 0.4rem;
      }
    }
  }
  .bottom {
    flex: 1;
    display: flex;
    // border: 1px dashed #596bd1;
    // margin: 0 0.5rem;
    border-radius: 0.04rem;
    // background: rgba(100, 114, 255, 0.14);
    opacity: 1;
  }
}
</style>
