<template>
  <div :ref="chartName" class="schoolTypeStaic">
    <div class="top">
      <img class="lta" src="../../assets/imgs/lta.png" alt="" />
      <img class="rba" src="../../assets/imgs/rba.png" alt="" />
      <div v-show="isShowChart1"  class="chart" :ref="chartName1" />
         <div v-if="!isShowChart1" class="noData">
      <img src="../../assets/imgs/index/nodata.png" alt="" />
      <p>暂无数据</p>
    </div>
      <!-- <div class="info">
        <div class="tit">学校总数：<span>36782</span>所</div>
        <div class="li">
          <div class="t"></div>
          传统公办型
          <span>1121</span>所
        </div>
        <div class="li">
          <div class="t"></div>
          传统公办型
          <span>1121</span>所
        </div>
        <div class="li">
          <div class="t"></div>
          传统公办型
          <span>1121</span>所
        </div>
      </div> -->
    </div>
    <div class="bottom">
      <img class="lta" src="../../assets/imgs/lta.png" alt="" />
      <img class="rba" src="../../assets/imgs/rba.png" alt="" />
      <div v-show="isShowChart2" class="chart" :ref="chartName2" />
         <div v-if="!isShowChart2" class="noData">
      <img src="../../assets/imgs/index/nodata.png" alt="" />
      <p>  {{errMsg || '暂无数据' }}</p>
    </div>
    </div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import Api from "../../utils/api";
//继承的基类
import DW from "../../../static/lib/DW";
export default {
  data() {
    return {
      chartName1: this.chartName + "1",
      chartName2: this.chartName + "2",
      chart1: this.chartName + "1",
      chart2: this.chartName + "2",
      isShowChart1:true,
      isShowChart2:true,
      dw: null,
      provider_id: this.provider_id,
      district_id: this.district_id,
      errMsg:''
    };
  },
  watch: {
    $route() {},
  },
  props: {
    chartName: {
      type: String,
    },
    base_url: {
      type: String,
      default: "",
    },
    token: {
      type: String,
      default: "",
    },
  },
  methods: {
    // 获取数据
    async getData() {
      const that = this;
      return new Promise((resolve) => {
        Api.SchoolTypeCensus.getApiData( 
          "SchoolTypeCensus",
          {
            provider_id: that.provider_id,
            district_id: that.district_id,
          },
          function (data) {
            if(data.list.source.lengt===0){
              that.isShowChart1 = false
            }
            resolve(data.list);
          }
        );
      });
    },

    // 获取学校增长数据
    async getSchoolAddCensus() {
      const that = this;
      return new Promise((resolve) => {
        Api.SchoolAddCensus.getApiData(
          "SchoolAddCensus",
          {
            provider_id: that.provider_id,
            district_id: that.district_id,
          },
          function (data) {
            resolve(data);
          },function(err){
            if(err==undefined){
              that.isShowChart2  = false
              that.errMsg = '获取数据失败'
            }
          }
        );
      });
    },

    // 绘制图表 必须
    async _drawLine() {
      let that = this;
      //getTheme获取主题；
      this.chart1 = echarts.init(
        that.$refs[`${that.chartName1}`],
        that.dw.getTheme()
      );

      const dataset = await that.getData();
      // console.log(dataset)
      this.chart1.setOption({
        //getDataset获取数据；
        dataset: [dataset],
        backgroundColor: "transparent",
        color: [
          "#FD3B73",
          "#6472FF",
          "#06D2C1",
          "#2C6DFF",
          "#FEBC31",
          "#FF7723",
        ],
        tooltip: {
        
        },
        title: {
          text: "{a|     学校类型}",
          show: true,
          textStyle: {
            color: "#C8CDFF",
            rich: {
              a: {
                fontSize: "100%",
                width: 15,
                height: 14,
                backgroundColor: {
                  image: require("../../assets/imgs/title.png"),
                },
              },
            },
          },
        },
        legend: {
          left: "40%",
          alight: "center",
          top: "28%",
          orient: "vertical",
          itemWidth: 10,
          itemHeight: 10,
          itemGap: 10,
          label: {},
          formatter: function (name) {
            return name;
          },
          textStyle: {
            rich: {
              a: {
                fontSize: "18",
                color: "#C8CDFF",
              },
            },
          },
        },
        series: [
          {
            type: "pie",
            radius: ["44%", "63%"],
            center: ["20%", "60%"],
            avoidLabelOverlap: false,
            roseType: "radius",
            name: "",
            smooth: true,
            // encode: {
            //   value: "num",
            //   itemName: "name",
            //   tooltip: ["name-num"],
            // },
            label: {
              normal: {
                show: false,
              },
            },
            emphasis: {
              label: {
                show: true,
                fontSize: "12",
                fontWeight: "bold",
              },
            },
            labelLine: {
              normal: {
                show: false,
              },
            },
            areaStyle: {},
          },
        ],
      });

      const dataset2 = await that.getSchoolAddCensus();

       const seriesData2 = [];
        dataset2.dimensions.forEach(item => {
          seriesData2.push({
            name: item.displayName,
            type: "line",
            smooth: true
          });
        });
      this.chart2 = echarts.init(
        that.$refs[`${that.chartName2}`],
        that.dw.getTheme()
      );
      this.chart2.setOption({
        dataset: [dataset2],
        backgroundColor: "transparent",
        title: {
          text: "{a|     增长情况}",
          show: true,
          textStyle: {
            color: "#C8CDFF",
            rich: {
              a: {
                fontSize: "100%",
                width: 15,
                height: 14,
                backgroundColor: {
                  image: require("../../assets/imgs/title.png"),
                },
              },
            },
          },
        },
        tooltip: {
          trigger: "axis",
        },
        legend: {
          type:'scroll',
          itemWidth: 10,
          itemHeight: 10,
          itemGap: 10,
          top: "16%",
          left: "center",
        },
        grid: {
          top: "40%",
          bottom: "10%",
        },
        xAxis: {
          type: "category",
           boundaryGap: true,
          axisTick: {
            //y轴刻度线
            show: false,
          },
          axisLine: {
            lineStyle: {
              color: "#6699FF",
            },
          },
        },
        yAxis: [
          {
            gridIndex: 0,
            axisLabel: {
              formatter: function () {
                return "";
              },
            },
            axisTick: {
              //y轴刻度线
              show: false,
            },
            axisLine: {
              lineStyle: {
                color: "#6699FF",
                opacity: "0.6",
              },
            },
          },
          {
            gridIndex: 0,
            axisLabel: {
              formatter: function () {
                return "";
              },
            },
            axisTick: {
              //y轴刻度线
              show: false,
            },
            axisLine: {
              lineStyle: {
                color: "#6699FF",
                opacity: "0.6",
              },
            },
          },
        ],
        series: seriesData2
      });
    },
    // 初始化 必须
    init() {
      this._dataInit();
      this.dw.resetDataset().init();
    },
    // 重渲染 必须
    reload() {
      this._dataInit();
      this.dw.render();
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 更改主题 必须
    changeTheme(theme) {
      let that = this;
      let blo = false;
      this.dw.getThemes().forEach(function (el) {
        if (el.id == theme) {
          blo = true;
        }
      });
      if (blo) {
        that.dw.setTheme(theme);
        try {
          that.chart1.dispose();
          that.chart2.dispose();
        } catch (e) {}
        that.dw.resetDataset("").init();
      } else {
        console.log(`${theme}该主题不存在`);
      }
    },
    // 设置其他参数 必须
    setSettings(obj) {
      this.dw.setSettings(obj);
    },
    // 设置机构id 必须
    setRangeId(obj) {
      this.dw.setRangeId(obj);
    },
    // 设置时间范围 必须
    setDateRange(obj) {
      this.dw.setDateRange(obj);
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 销毁图表及数据初始化 必须
    _dataInit() {
      try {
        this.chart1.dispose();
        this.chart2.dispose();
      } catch (e) {}
    },
  },
  mounted: function () {
    let that = this;
    //创建实例 组件ref,api根路径,鉴权token 必须
    that.dw = new DW(`${that.chartName}`, that.base_url, that.token);
    //请求数据 必须
    that.dw.load_data = async function () {
      // let assetsData = await that.main_statistic();
      // let consumeData = await that.consume_statistic();
      let assetsData = [];
      let consumeData = [];
      return new Promise((resolve) => {
        resolve([assetsData, consumeData]);
      });
    };
    //渲染页面 必需
    that.dw.render = async function () {
      //除了绘制图表其他的事
      // that._doFn();
      //绘制图表
      that._drawLine();
    };
    //定义主题 非必须
    that.dw.getThemes = function () {
      return [
        { id: "dark", name: "暗色主题" },
        { id: "light", name: "亮色主题" },
      ];
    };
  },
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
@function px2em($px) {
  @return ($px/75) * 1rem;
}
@mixin textOver {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.chart {
  width: 100%;
  height: 100%;
  padding: 0.1rem 0.2rem;
  box-sizing: border-box;
}
.schoolTypeStaic {
  .rba {
    position: absolute;
    bottom: 0;
    right: 0;
  }
  .lta {
    position: absolute;
    top: 0;
    left: 0;
  }
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 0.4rem 0.3rem 0.2rem 0.3rem;
  background: url("../../assets/imgs/school/wrapschool.png") no-repeat top
    center/100% 100%;
  .top {
    position: relative;
    flex: 1;
    background: rgba(59, 157, 255, 0.14);
    margin-bottom: 0.1rem;
    position: relative;
    .info {
      position: absolute;
      right: 0;
      top: 5%;
      width: 2.16rem;
      right: 0.3rem;
      padding: 0.1rem 0.2rem;
      box-sizing: border-box;
      height: 90%;
      border: 1px dashed #28406c;
      .tit {
        font-size: 0.14rem;
        color: #c8cdff;
        span {
        }
      }
      .li {
        display: flex;
        align-items: center;
        font-size: 0.12rem;
        color: #c8cdff;
        line-height: 2;
        .t {
          width: 0.1rem;
          height: 0.05rem;
          background: #f5ba3f;
          opacity: 1;
          border-radius: 0.1rem;
          margin-right: 0.1rem;
        }
        span {
          color: #ffa44a;
          font-size: 0.16rem;
          margin: 0 0.04rem;
        }
      }
    }
  }
  .bottom {
    position: relative;
    flex: 1;
    background: rgba(100, 114, 255, 0.14);
    opacity: 1;
  }
}
</style>
